import React from 'react';
import ReactDOMServer from 'react-dom/server';
import _ from 'lodash';

import Html from './static/Html';
import App from './components/App';

export default (locals, callback) => {
  const clientChunk = _.find(locals.webpackStats.compilation.chunks, x => x.name === 'client');
  const cssUrl = _.find(clientChunk.files, x => /\.css$/.test(x));
  const jsUrl = _.find(clientChunk.files, x => /\.js/.test(x));

  const markup = ReactDOMServer.renderToString(<App />);
  const html = ReactDOMServer.renderToStaticMarkup(<Html jsUrl={jsUrl} cssUrl={cssUrl} markup={markup}/>);
  callback(null, `<!doctype html>\n${html}\n`);
};



