import React from 'react';
import Component from 'react-pure-render/component';
import axios from 'axios';
import validator from 'validator';

import mandrilConfig from '../../data/mandrill.yml';
import Spinner from './Spinner';

const MODE_PROGRESS = 'progress';
const MODE_SENT = 'sent';
const MODE_ERROR = 'error';
const MODE_INVALID = 'invalid';

function hashToMandrill(obj) {
  const result = [];
  Object.keys(obj).forEach(key => {
    result.push({
      name: key,
      content: obj[key]
    });
  });
  return result;
}

function sendMail(data) {
  return axios({
    method: 'POST',
    url: 'https://mandrillapp.com/api/1.0/messages/send-template.json',
    data: {
      key: mandrilConfig.key,
      template_name: mandrilConfig.template,
      template_content: hashToMandrill(data),
      message: {
        to: mandrilConfig.recipients.map(email => ({email}))
      }
    }
  });
}

export default class Feedback extends Component {

  constructor() {
    super();
    this.state = {
      mode: null
    };
  }

  submitFeedback = async (e) => {
    e.preventDefault();

    const name = validator.trim(this.refs.name.value);
    const email = validator.trim(this.refs.email.value);
    const text = validator.trim(this.refs.text.value);

    if (!validator.isEmail(email) || !name.length || !text.length) {
      this.setState({
        mode: MODE_INVALID
      });
      return;
    }

    this.setState({
      mode: MODE_PROGRESS
    });

    try {
      await sendMail({name, email, text});

      this.setState({
        mode: MODE_SENT
      });

      this.refs.name.value = '';
      this.refs.email.value = '';
      this.refs.text.value = '';
    } catch (err) {
      this.setState({
        mode: MODE_ERROR
      });
    }
  };

  render() {
    const { mode } = this.state;
    const submitButton = (
      <input type='submit' className={styles.submit} value=''
             onClick={this.submitFeedback}/>
    );

    const email = <a className={styles.emailTo} href='mailto:ieshuagn@gmail.com'>ieshuagn@gmail.com</a>;

    let submitResult = null;
    if (mode === MODE_SENT) {
      submitResult = (
        <div className={styles.submitResult}>
          ,       .
        </div>
      );
    } else if (mode === MODE_ERROR) {
      submitResult = (
        <div className={styles.submitResult}>
          -   .    <br />
          , ,   {email}.
        </div>
      );
    } else if (mode === MODE_INVALID) {
      submitResult = (
        <div className={styles.submitResult}>
          ,    .
        </div>
      );
    }

    return (
      <div className={styles.root}>
        <div className={styles.content}>
          <h4>   </h4>
          <p>
                  {email}   +62 822 372 853 31
          </p>
          <form onSubmit={this.submitFeedback}>
            <input type='text' placeholder=' ' ref='name'/>
            <input type='text' placeholder=' ' ref='email'/>
            <textarea placeholder='  ' ref='text'>
            </textarea>
            {mode === MODE_PROGRESS
              ? <div className={styles.spinnerWrapper}><Spinner /></div>
              : submitButton
            }
            {submitResult}
          </form>
        </div>
      </div>
    );
  }

}



